  content: "";
  width: 9px;
  height: 9px;
  border: 1px solid #c0c0c0;
  border-left: none;
  border-bottom: none;
  transform: rotate(45deg);
}
.audit-steps-section-line--left span {
  padding-left: 20px;
  margin-left: auto;
}
.audit-steps-section-line--left::after {
  position: absolute;
  top: 6px;
  left: 0;
  content: "";
  width: 9px;
  height: 9px;
  border: 1px solid #c0c0c0;
  border-right: none;
  border-top: none;
  transform: rotate(45deg);
}

