.social {
  &__list {
    @extend .flex-wrap;
    height: 100%;
    gap: 16px;
  }

  &__list-item {

    a {
      @extend .flex-centered-content;
      width: 100%;
      height: 100%;

      svg {
        transition: fill var(--main-transition);

        &:hover,
        &:focus {
          fill: var(--accent);
          opacity: 1;
        }
      }
    }
  }
}
